@import 'vars';

.contacts__map {
    height: 575px;
    width: 100%;
    margin-bottom: 10px;
}

.page-contacts__bg {
    background-color: #fff;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 38px 107px 50px;
    margin-top: 20px;
}

.page-contacts__breadcrumbs {
    padding-left: 10px;
    margin: 15px 0;
}


.page-contacts__item {
    width: 50%;
    .socials .socials__item {
        margin-right: 5px;
    }
    .socials .socials__link {
        width: 26px;
    }
}


.page-contacts__col-right {
    display: flex;
    background-color: #fff;
    width: 50%;
    justify-content: flex-end;   
}

.page-contacts__col-right .page-contacts__item:last-child {
    width: auto;
}

.page-contacts__col-left {
    display: flex;
    background-color: #fff;
    width: 50%;
    justify-content: space-between;
}

.socials__list {
    font-size: 0;
}

.page-contacts__title {
    color: map_get($gray-light, default);
    margin-bottom: 20px;
}

.page-contacts__desc {
    color: #2b2b2b;
    font-size: 14px;
    font-weight: 400;
    a {
        color: map_get($blue, default);
        font-weight: 500;
        text-decoration: underline;
        cursor: pointer;
        display: block;
    }
}

.page-contacts__desc--schedule {
    text-transform: uppercase;
    display: table-row;
    .schedule-day {
        display: table-cell;
        padding-bottom: 4px;
    }
    .schedule-time {
        display: table-cell;
        padding-bottom: 4px;
        padding-left: 5px;
        &--dayoff {
            text-transform: none;
        }
    }
}

